* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Work Sans", sans-serif;
  font-size: 16px;
}

.header {
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.5rem;
  font-weight: bold;
  width: 20%;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.nav {
  display: flex;
  gap: 20px;
  padding: 10px;
  margin: 10px;
}

.nav p {
  cursor: pointer;
}

.nav-toggle {
  display: none;
}

.hero {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  background-image: url("https://tf-frontend.netlify.app/images/v1/bgimage.png");
}

.hero h1 {
  font-size: 4rem;
  letter-spacing: 1.5px;
  margin-bottom: 2rem;
  color: white;
  font-family: "Red Hat Display", sans-serif;
  text-align: center;
}

.hero p {
  font-size: 1rem;
  font-weight: lighter;
  color: white;
  margin-bottom: 1.5rem;
  text-align: center;
  max-width: 600px;
  line-height: 1.5;
}

.button {
  padding: 0.8rem 2rem;
  background: #27ae60;
  color: white;
  border: none;
  text-decoration: none;
  z-index: 1;
}
.button-2 {
  padding: 0.8rem 2rem;
  background: black;
  color: white;
  border: none;
  text-decoration: none;
  z-index: 1;
}

.button:hover {
  background: #f78bd8;
}
.button-2:hover {
  background: #f78bd8;
}

.main {
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.main h2 {
  text-align: center;
  margin: 3.5rem;
  font-size: 2.5rem;
  font-family: "Red Hat Display", sans-serif;
  letter-spacing: 1px;
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.card {
  overflow: hidden;
  position: relative;
}

.card-img {
  position: relative;
}

.card img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.card p {
  font-family: work sans;
  font-size: 0.875rem;
}

.tag {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 0.3rem 1rem;
  background-color: #ff69b4;
  color: white;
  border-radius: 20px;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
  z-index: 1;
}

.card-content {
  padding: 1rem;
}

.date {
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

.tag-b {
  background-color: #27ae60;
}

.cta {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.button-cta {
  padding: 0.8rem 2rem;
  background: #27ae60;
  color: white;
  border: none;
  border-radius: 4px;
  text-decoration: none;
}

@media (max-width: 768px) {
  .logo {
    width: 60%;
  }
  .nav {
    display: none;
  }
  .nav.show {
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 10px;
    background-color: white;
  }
  .nav-toggle {
    display: block;
    background-color: white;
  }

  .nav-toggle img {
    display: block;
    width: 50%;
    margin-left: auto;
    background-color: white;
    z-index: 1;
  }
  .nav {
    display: none;
  }
 
}


@media screen and (min-width: 992px) {
  .hero::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("./Components/ondas/ondas izda.png");
    background-size: contain;
    background-position: left bottom;
    background-repeat: no-repeat;
    z-index: 1;
  }

  .hero::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("./Components/ondas/ondas dcha.png");
    background-size: contain;
    background-position: right top;
    background-repeat: no-repeat;
    z-index: 1;
  }
}
